.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}
body {
  background-color: rgb(34, 123, 146);
  min-height: 200vh; 
  font-size: 1rem;
}

h3, h1, p{
  line-height: 2.5;
  text-align: center;
}

.header{
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 1rem;
  width: 100%;
}

.navbar {
  background-color: rgb(136, 197, 211);
  padding: 1rem 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(4, 134, 4);
  border-radius: 40px;
  width: 100%;
  max-width: 900px;
}

.navbar-list {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  flex-wrap: wrap;
  justify-content: center;
}

.navbar-list li {
  margin: 0.5rem 2vw;
  transition: transform 0.2s ease-in-out;
}

.navbar-list li:hover {
  transform: scale(1.1);
}
.navbar-list a {
  text-decoration: none;
  color: rgb(9, 98, 121);
  font-weight: bold;
  padding: 0.3rem 0.6rem;
  display: block;
  white-space: nowrap;
}

.navbar-list a:hover {
  color: black;
  background-color: rgb(136, 197, 211);
  border-radius: 10px;
}

.Me img {
  padding: 7px;
  background-color: white;
  max-width: 100%;
  height: auto;
  width: 300px;
}

.Me {
  padding: 2rem;
  background-color: rgb(136, 197, 211);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5%;
  width: 90%;
  max-width: 900px;
  margin: 0 auto;

}

.Info {
  padding: 1rem;
  line-height: 2.0;
  font-size: 1.25rem;
  margin: 4rem 0;
}

#experience {
  padding: 3rem 1rem;

}

.experience-cards {
  padding: 1rem;
  display: flex;
  gap: 2vw;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 2rem;

}

.experience-card {
  background-color: white;
  padding: 1.25rem;
  width: 100%;
  max-width: 350px;
  min-width: 280px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  transition: transform 0.2s ease;
}

.experience-card:hover {
  transform: scale(1.05);
  color: rgb(9, 98, 121);
  background-color: rgb(136, 197, 211);
}

.experience-card h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.experience-card p {
  font-size: 0.95rem;
  line-height: 1.6;
  text-align: left;
}

#socials {
  padding: 3rem 1rem;
}

.LinkedInIcon {
  color: rgb(136, 197, 211);
  display: flex;
  justify-content: center;
  transition: transform 0.2s ease-in-out;
}

.LinkedInIcon:hover{
  cursor: pointer;
  transform: scale(1.1);
}


#resume {
  padding: 3rem 1rem;
}

.resume {
  padding: 1.25rem;
  background-color: rgb(136, 197, 211);
  font-size: 1.2rem;
  max-width: 100%;
  display: flex;
  justify-content: center;
}

.resume img {
  max-width: 100%;
  height: auto;
  width: 100%;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: white;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(136, 197, 211);
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgb(101, 179, 197);
}

/* Tablet */
@media (max-width: 1024px) {
  html{
    font-size: 15px;
  }
  
  .navbar-list li{
    margin: 0.5rem 1.5vw;
  }

  .Me {
    flex-direction: column;
    width: 90%;
  }

  .Me img {
    width: 250px;
  }

  .experience-card {
    width: calc(50% - 1vw);
    min-width: 280px;
  }

  .Info {
    font-size: 1.1rem;
    margin: 2rem 0;
  }
}

/* Mobile */
@media (max-width: 768px) {
  html {
    font-size: 15px;
  }

  .header {
    padding: 0.5rem;
  }
  
  .navbar {
    padding: 0.75rem 0.25rem;
    border-radius: 25px;
  }

  .navbar-list li{
    margin: 0.25rem 3vw;
  }

  .navbar-list a {
    font-size: 0.9rem;
    padding: 0.25rem 0.5rem;
  }

  .Me {
    flex-direction: column;
    width: 95%;
    padding: 1.5rem;
  }

  .Me img {
    width: 200px;
  }

  .experience-card {
    width: 100%;
    max-width: 450px;
  }

  .experience-card h1 {
    font-size: 1.3rem;
  }

  .Info {
    font-size: 1rem;
    margin: 1.5rem 0;
    padding: 0.5rem;
  }

  #experience, #socials, #resume {
    padding: 2rem 0.5rem;
  }

  h1 {
    font-size: 1.75rem;
  }

  h3 {
    font-size: 1.25rem;
  }
}

/* Extra small mobile */
@media (max-width: 480px) {
  html {
    font-size: 14px;
  }

  .navbar-list {
    gap: 0.25rem;
  }

  .navbar-list li {
    margin: 0.25rem 2vw;
  }

  .navbar-list a {
    font-size: 0.85rem;
    padding: 0.2rem 0.4rem;
  }

  .Me {
    padding: 1rem;
  }

  .Me img {
    width: 150px;
  }

  .Info {
    font-size: 0.95rem;
    line-height: 1.8;
  }

  .experience-card {
    padding: 1rem;
  }

  .experience-card h1 {
    font-size: 1.2rem;
  }

  .experience-card p {
    font-size: 0.9rem;
  }

  h1 {
    font-size: 1.5rem;
    line-height: 2;
  }

  h3 {
    font-size: 1.1rem
  }
}
